@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;600&display=swap);
html,
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: var(--textTitle);
  background-color: var(--bg);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-family: "Raleway", sans-serif;
}

body.light {
  --bg: #fff;
  --bg-secondary: #efefef;
  --textTitle: #222;
  --card-bg: #fff;
  --region-hover: #eee;
  --spinner-track-color: rgba(195, 195, 195, 0.6);
  --spinner-track: #636767;
  --form-shadow: 0 2px 10px 0 #ccc;
  --card-shadow: 0 0 7px 3px #eee;
  --location-btn-bg: #dee1f1;
  --location-btn-shadow: 0 2px 2px 1px #bec0d0;
}

body.dark {
  -webkit-font-smoothing: antialiased;
  --bg: #1e213a;
  --bg-secondary: #100e1d;
  --textTitle: #e7e7eb;
  --card-bg: #1e213a;
  --region-hover: #292929;
  --spinner-track-color: #757575;
  --spinner-track: #eee;
  --form-shadow: 0 2px 10px 0 #222;
  --card-shadow: 0 2px 10px 0 #222;
  --location-btn-bg: #6e707a;
  --location-btn-shadow: 0 2px 2px 1px #181a2d;
}

.page-container {
  overflow: hidden;
}

.left-panel {
  width: 370px;
  background-color: var(--bg);
  color: var(--textTitle);
  height: 100vh;
  position: fixed;
  top: 0;
}

.left-panel::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.05;
  background: #1e213a url(/weather-app/static/media/cloud-background.11452cab.png) no-repeat 50% 15%;
}

.right-panel {
  background-color: var(--bg-secondary);
  height: 100vh;
  padding: 20px 70px;
  margin-left: 370px;
  box-sizing: border-box;
  overflow: auto;
}

.location-error {
  font-size: 24px;
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.forcase-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.material-icons {
  font-size: 16px;
}

.spinner {
  width: 100%;
  height: calc(100vh - 172px);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
}

.spinner div {
  width: 28px;
  height: 28px;
  background-color: white;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
          animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}
.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}
.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}
@-webkit-keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}
@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

.spinner-round {
  width: 50px;
  height: 50px;
  margin: 50px auto;
  background-color: grey;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1s infinite ease-in-out;
          animation: sk-scaleout 1s infinite ease-in-out;
}

@-webkit-keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}

@media only screen and (max-width: 768px) {
  .left-panel {
    width: 100%;
    position: static;
  }

  .right-panel {
    margin-left: 0;
    padding: 20px;
	overflow: hidden;
	height: auto;
  }

  .forcase-list {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.forecast-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background: var(--card-bg);
  color: var(--textTitle);
  padding: 15px 20px;
  margin: 10px;
  border-radius: 5px;
}

.forecast-date {
  padding-bottom: 16px;
}

.forecast-temp {
  display: flex;
  align-items: center;
  padding-top: 16px;
}

.forecast-temp div {
  padding: 0 10px;
}

.search-page {
  display: flex;
  flex-direction: column;
  padding: 20px;
  position: relative;
  top: 0;
  height: calc(100vh - 40px);
  z-index: 100;
  background: var(--bg);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.search-page.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.search-page.close {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.back-section {
  display: flex;
  justify-content: flex-end;
}

span.back-btn {
  font-size: 24px;
  cursor: pointer;
}

.back-btn:hover {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: -webkit-transform 0.6s;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
}

.search-section {
  margin: 30px 0;
}

.search-box {
  height: 38px;
  width: 58%;
  margin-right: 10px;
  padding: 0 10px;
  background: transparent;
  border: 1px solid var(--textTitle);
  color: var(--textTitle);
}

::-webkit-input-placeholder {
  color: var(--textTitle);
  opacity: 0.65;
}

::-moz-placeholder {
  color: var(--textTitle);
  opacity: 0.65;
}

:-ms-input-placeholder {
  color: var(--textTitle);
  opacity: 0.65;
}

::-ms-input-placeholder {
  color: var(--textTitle);
  opacity: 0.65;
}

::placeholder {
  color: var(--textTitle);
  opacity: 0.65;
}

.search-btn {
  background: #3c47e9;
  border: 1px solid #3c47e9;
  color: #e7e7eb;
  padding: 6px 12px;
  line-height: 2;
  cursor: pointer;
}

.search-btn:hover,
.search-btn:focus {
  -webkit-filter: contrast(3);
          filter: contrast(3);
  outline: none;
}

.search-btn:active {
  -webkit-filter: contrast(3.5);
          filter: contrast(3.5);
}

.search-result {
  overflow-y: auto;
}

.search-result::-webkit-scrollbar {
  width: 10px;
}

.search-result::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}

.search-result::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.search-list {
  display: flex;
  flex-direction: column;
}

.search-list-item {
  padding: 20px 10px;
  margin: 5px 0;
  border: 1px solid transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-list-item .material-icons {
  visibility: hidden;
}

.search-list-item:hover {
  border: 1px solid #4b4e62;
}

.search-list-item:hover .material-icons {
  visibility: visible;
}

@media only screen and (max-width: 768px) {
  .search-page {
    position: fixed;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
}

.current-temp {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.current-temp.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.current-temp.close {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.change-location {
  margin-bottom: 20px;
  padding: 20px;
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
}

.location-btn {
  background: var(--location-btn-bg);
  color: var(--textTitle);
  border: 0;
  padding: 8px 16px;
  line-height: 1.5;
  box-shadow: var(--location-btn-shadow);
  cursor: pointer;
}

.location-btn:hover,
.location-btn:focus {
  -webkit-filter: saturate(4.5);
          filter: saturate(4.5);
  outline: none;
}

.material-icons.current-location {
  font-size: 22px;
  background: var(--location-btn-bg);
  color: var(--textTitle);
  padding: 7px;
  border-radius: 50%;
  cursor: pointer;
}

.material-icons.current-location:hover,
.material-icons.current-location:focus {
  -webkit-filter: saturate(4.5);
          filter: saturate(4.5);
}

.current-weather-img {
  width: 115px;
}

.today-temp {
  padding: 3rem 0 1rem 0;
  font-size: 6rem;
}

.today-weather {
  padding: 1rem 0 2rem 0;
  font-size: 2rem;
}

.today-date {
  padding: 4rem 0 0rem 0;
}

.chosen-location {
  padding: 1rem 0;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .current-weather-img {
    width: 90px;
  }

  .today-temp {
    padding-top: 1.5rem;
    font-size: 4rem;
  }

  .today-weather {
    padding-bottom: 1rem;
  }
}

.today-highlight {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 40px;
}

.wind,
.humidity,
.visibility,
.air-pressure {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  flex-basis: 280px;
  background-color: var(--card-bg);
  color: var(--textTitle);
  padding: 20px;
  border-radius: 5px;
}

.hightlight-value {
  font-size: 3.5rem;
  font-weight: 600;
  padding: 10px 0;
}

.highlight-unit {
  font-size: 1.5rem;
}

.compass {
  display: flex;
  align-items: center;
}

.compass .material-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--location-btn-bg);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin-right: 18px;
}

.progress-bar-scale {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 3px;
}

.progress-bar-track {
  width: 80%;
  background: #e7e7eb;
  border-radius: 10px;
}

.progress-bar-fill {
  color: #000;
  background-color: #c88c32;
  height: 8px;
  border-radius: 10px;
}

@media only screen and (max-width: 768px) {
  .today-highlight {
    grid-template-columns: auto;
  }

  .hightlight-value {
    font-size: 2.5rem;
  }

  .compass {
    font-size: 0.9rem;
  }

  .compass .material-icons {
    font-size: 14px;
    height: 24px;
    width: 24px;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  margin-bottom: 30px;
}

.dark-mode {
  display: flex;
  align-items: center;
  cursor: pointer;
  background: var(--card-bg);
  border-radius: 50%;
  padding: 10px;
}

.change-temp-format {
  display: flex;
  align-items: center;
}

.temp-format {
  font-size: 16px;
  font-weight: 600;
  background: #585676;
  color: var(--textTitle);
  padding: 7px 9px;
  margin-left: 10px;
  border-radius: 50%;
  cursor: pointer;
}

.temp-format.selected {
  background: var(--textTitle);
  color: var(--card-bg);
}

